import { Injectable } from '@angular/core';
import { HttpSerProvider } from "../http-ser/http-ser";

/*
  Generated class for the ApiCallerProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ApiCallerProvider {

  constructor(public httpSer: HttpSerProvider) {
    console.log('Hello ApiCallerProvider Provider');
  }

  checkVersion(appName, version) {
    let url: string = 'http://htopcloud.chinaoak.cn:18888/api/sys/checkVersion';
    return this.httpSer.get(url, {'app': appName, 'version': version});
  }

}



