import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Generated class for the HttpSerProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class HttpSerProvider {

  constructor(public http: HttpClient) {
    console.log('Hello HttpSerProvider Provider');
  }

  get(url:string, paramObj:any = {}) {
    let timestamp = this.getTimestamp();
    return this.http.get(url + this.toQueryString(paramObj, timestamp))
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  /**
   * 获取当前时间 减 2015年1月1日的 时间戳
   */
  private getTimestamp() {
    let timestamp = Math.floor(new Date().getTime() / 1000 - 1420070400).toString();
    return timestamp;
  }

  private extractData(res: any) {
    if(res.code == 200){
      let data = JSON.parse(res.data);
      return data || {};
    }
    else{
      return Promise.reject(res.message);
    }
  }

  private handleError(error:Response | any) {
    let errMsg:string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Promise.reject(errMsg);
  }

  private toQueryString(obj, timestamp) {
    if(obj) {
      let ret = [];
      for (let key in obj) {
        key = encodeURIComponent(key);
        let values = obj[key];
        if (values && values.constructor == Array) {
          //array
          let queryValues = [];
          for (let i = 0, len = values.length, value; i < len; i++) {
            value = values[i];
            queryValues.push(this.toQueryPair(key, value));
          }
          ret = ret.concat(queryValues);
        } else {
          //string
          ret.push(this.toQueryPair(key, values));
        }
      }

      //add timestamp
      ret.push(this.toQueryPair('timestamp', timestamp));

      return '?' + ret.join('&');
    }
    else{
      return '?timestamp=' + timestamp;
    }
  }

  private toQueryPair(key, value) {
    if (typeof value == 'undefined') {
      return key;
    }
    return key + '=' + encodeURIComponent(value === null ? '' : String(value));
  }

}



